<template>
     <div class="modal fade in" id="edithospitalUnitInfo" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" style="display: none;">
    <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
            <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true"></span></button>
                <h4 class="modal-title" id="exampleModalLabel"></h4>
            </div>
            <div class="modal-body forms">
                <div class="form-inline">
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label" for="formNo"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-10">
                            <input type="text" id="name" class="form-control" placeholder="" v-model="$parent.$data.partmentModel.name.name" v-bind:class="{haserror:($parent.$data.partmentModel.name.iserror)}">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label" for="formNo"></label>
                        </div>
                        <div class="col-sm-10">
                            <input type="text" id="description" class="form-control" placeholder="" v-model="$parent.$data.partmentModel.desc">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label" for="formNo"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <select id="receipt" class="form-control" v-model="$parent.$data.partmentModel.tickets.name" v-bind:class="{haserror:($parent.$data.partmentModel.tickets.iserror)}">
                                <option value=""></option>
                            <option value="7d463995-5007-4922-9adc-0dafc9a45ed9"></option></select>
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label" for="formNo"></label>
                        </div>
                        <div class="col-sm-4">
                            <select id="businessType" class="form-control" v-model="$parent.$data.partmentModel.type">
                                <option value="1"></option>
                                <option value="2"></option>
                            </select>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="shortCode" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.partmentModel.abbr">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactPerson" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.partmentModel.contactName.name" v-bind:class="{haserror:($parent.$data.partmentModel.contactName.iserror)}">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-10">
                            <input type="text" id="address" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.partmentModel.contactAddress">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label">1</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay1" class="form-control" placeholder="1" maxlength="50" v-model="$parent.$data.partmentModel.contactMethod1">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label">2</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay2" class="form-control" placeholder="2" maxlength="50" v-model="$parent.$data.partmentModel.contactMethod2">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label">3</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay3" class="form-control" placeholder="3" maxlength="50" v-model="$parent.$data.partmentModel.contactMethod3">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label">4</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay4" class="form-control" placeholder="4" maxlength="50" v-model="$parent.$data.partmentModel.contactMethod4">
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-primary" id="btnConfirm" @click="applyEditPartment"></button>
                <button type="button" class="btn btn-default" data-dismiss="modal" id="btnClose"></button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'addPartment',
  data () {
    return {    
         }
  },
  methods:{   
       //
      applyEditPartment(){
        var _this = this.$parent.$data;
              //      
        if (_this.partmentModel.name.name =="") {
                _this.partmentModel.name.iserror = true;
        }else{
            _this.partmentModel.name.iserror = false;
        };
        if (_this.partmentModel.tickets.name =="") {
                _this.partmentModel.tickets.iserror = true;
        }else{
            _this.partmentModel.tickets.iserror = false;
        };
        if (_this.partmentModel.contactName.name =="") {
                _this.partmentModel.contactName.iserror = true;
        }else{
            _this.partmentModel.contactName.iserror = false;
        };
    var isclick = _this.partmentModel.name.iserror||_this.partmentModel.contactName.iserror||_this.partmentModel.tickets.iserror;
      if(!isclick){
        var data = {
          name:_this.partmentModel.name.name,
          description:_this.partmentModel.hospitalDesc,
          abbr:_this.partmentModel.hospitalAbbr,
          tickets:_this.partmentModel.tickets.name,
          type:_this.partmentModel.type,
          contactName:_this.partmentModel.contactName.name,
          contactAddress:_this.partmentModel.contactAddress,
          contactMethod1:_this.partmentModel.contactMethod1,
          contactMethod2:_this.partmentModel.contactMethod2,
          contactMethod3:_this.partmentModel.contactMethod3,
          contactMethod4:_this.partmentModel.contactMethod4,
          hostipalId:_this.partmentModel.hostipalId       
      };
         this.$http.post("url",data).then(response => {
            alert("")
        },response => {
                // error callback
                console.log("")
              });
      $("#edithospitalUnitInfo").modal("toggle")
      }; 
      },
  }
}
</script>
<style>  
</style>


